#PageButtons-LeftArrow {
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
    border-right-width: 0px;

    margin-top: 0px;
    margin-bottom: 0px;
}

#PageButtons-PageBtn {
    border-radius: 0px;
    background-color: #565d63;

    margin-top: 0px;
    margin-bottom: 0px;
}

#PageButtons-PageBtn:hover {
    background-color: black;
}

#PageButtons-RightArrow {
    border-bottom-left-radius: 0px;
    border-top-left-radius: 0px;
    border-left-width: 0px;

    margin-top: 0px;
    margin-bottom: 0px;
}

#PageButtons-Container {
    display: flex;
    align-items: center;
}

#PageButtons-ReadOnlyText {
    font-family: 'Oswald', sans-serif;
    font-size: 20px;
    margin-right: 10px;
}
