.selected-entity__container {
    font-family: 'Oswald', sans-serif;
    flex-direction: column;
    justify-content: center;
    margin-top: 5px;
    margin-bottom: 5px;
}

.selected-entity__full-text {
    margin: 5px;
}

.selected-entity__connection,
.selected-entity__tag {
    font-family: 'Oswald', sans-serif;
    color: #f9f9f9;
    font-weight: bold;
    border-radius: 2px;
    padding: 2px 4px;
    margin-right: 5px;
    cursor: pointer;
}

.selected-entity__tag {
    background-color: #17a2b8 !important;
}

.selected-entity__select {
    font-family: 'Oswald', sans-serif;
    font-weight: bold;
    background-color: lightgray;
    border-radius: 2px;
    padding: 2px 4px;
    margin-right: 5px;
    top: 30px;
    cursor: pointer;
}

.selected-entity__textarea {
    padding: 5px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
}

.selected-entity__offset-inputs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: flex-start;
}

.selected-entity__offset-input::-webkit-slider-thumb,
.selected-entity__offset-input::-moz-range-thumb,
.selected-entity__offset-input::-ms-thumb {
    border: 1px solid #000000;
    height: 13px;
    width: 13px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
}

.selected-entity__offset-input {
    width: 50%;
    -webkit-appearance: none;
    background: transparent;
    height: 6px;
    cursor: pointer;
    -webkit-appearance: none;
    background: grey;
}

#selected-entity__offset-input-start-text {
    display: flex;
    width: 50%;
    justify-content: center;
    font-size: 12px;
    opacity: 0.5;
}

#selected-entity__offset-input-end-text {
    display: flex;
    width: 50%;
    justify-content: center;
    font-size: 12px;
    opacity: 0.5;
}

.selected-entity__reset {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    width: 100%;
}

.selected-entity__unallowed-text {
    margin-left: 5px;
    margin-bottom: 0px;
}

.selected-entity__unselected-text {
    color: lightgray;
    font-size: smaller;
}

.selected-entity__unassigned-tag {
    background-color: red !important;
}

.selected-entity__dropdown-caret {
    padding: 2px !important;
    margin-right: 5px;
    position: relative;
    bottom: 3px;
}

.selected-entity__dropdown-container {
    display: inline;
}

.selected-entity__dropdown-container > .dropdown-menu {
    padding: 0px 5px;
    min-width: 20px;
}

.selected-entity__dropdown-container > .dropdown-menu > .dropdown-item {
    padding: 0px 5px;
}

.selected-entity__dropdown-container > .dropdown-menu > .dropdown-item:hover {
    background-color: lightgray;
}
