.FunctionalButton {
    font-family: 'Oswald', sans-serif;
    font-weight: bold;
    font-size: 20px;
    max-width: 200px;
    margin-top: 5px;
    margin-bottom: 10px;
}

.TagButton {
    margin: 4px;
    font-family: 'Oswald', sans-serif;
    color: #f9f9f9;
    font-weight: bold;
    font-size: 20px;
}

.MenuItem {
    font-family: 'Oswald', sans-serif;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
}

.TopTagButton {
    margin: 2px;
    font-family: 'Oswald', sans-serif;
    color: #f9f9f9;
    font-weight: bold;
    font-size: 18px;
    text-align: left;
}


.TagButtonClicked {
    background: #ffe184;
    color: #353C46;
    font-family: 'Oswald', sans-serif;
    font-size: 24px;
    font-weight: bold;
    border: 0;
}

.SentimentBarButton {
    font-size: 16px;
    color: #353C46;
    border: 0
}

.SentimentSelectorButton {
    margin: 0;
    padding: 0.4rem;
    font-family: 'Oswald', sans-serif;
    font-weight: 700;
    font-size: 1.0rem;
    border: 0;
}


.SvgCancelButtonTag {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 1.2em;
    padding: 0;
    margin: 0;
    color: #404040;
    background: rgba(234, 63, 85, 0.0);
    position: absolute;
    top: -12px;
    right: -12px;
    z-index: 100;
}