.App {
    display:flex;
    flex-direction:column;
    height: 100%;
}

.AppLogo {

}

.AppTopNav {
    background: #384450;
    height: 45px;
    min-width: 100%;
    display:flex;
    flex-direction:row;
    justify-content: space-between;
}

.LogoSection {
    padding: 0;
    margin: 0 60px 0 10px;
    flex-direction: row;
    position: relative;
}

.LogoFont {
    color: #fff;
    font-size: 1.7em;
    background: #384450;
    padding: 5px 0 0 0;
    font-family: Roboto Condensed;
}


.AppBody {
    display:flex;
    flex-direction:row;
    height: calc(100% - 45px);
}


.EditorControlColumn {
    background: #495a6d;
    min-height: 100%;
    height: 100%;
    flex: 0 0 auto;
    overflow:auto;
}

.WorkshopAreaColumn {
    min-height: 100%;
    width: 100%;
    overflow:auto;
}

.PageContainer {
    padding: 1.5rem;
}

.AnnotatorArea {
    margin-right: auto;
    margin-left: auto;
}

button:disabled {
    cursor: not-allowed !important;
}
