.HighlightSpan {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    line-height: inherit;
    background: #ffe184;
    margin: 0.25rem 0rem 0.25rem;
    padding: 0.25rem 0rem 0.25rem;
    position: relative;
    cursor: pointer;
    /*z-index: 0;*/
}

.OuterHighlightSpan {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    line-height: inherit;
    margin: 1.4rem 0 0.5rem;
    padding: 1.4rem 0 0.5rem;
    position: relative;
    cursor: pointer;
}

.InnerHighlightSpan {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    line-height: inherit;
    margin: 0.15rem 0rem 0.15rem;
    padding: 0.15rem 0rem 0.15rem;
    position: relative;
    cursor: pointer;
    z-index: inherit;
}

.BackgroundHighlightSpan {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    line-height: inherit;
    background: rgba(255, 225, 132, 0.6);
    margin: 0.25rem 0 0.25rem 0;
    padding: 0.25rem 0 0.25rem 0;
    opacity: 0.6;
    position: relative;
    cursor: pointer;
    z-index: 0;
    text-decoration: line-through;
}

.RelationHighlightSpan {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    line-height: inherit;
    margin: 0.25rem;
    padding: 0.25rem;
    position: relative;
    cursor: pointer;
    z-index: 0;
    border: 1px solid;
    border-radius: 0.4em;
}

.CoreferenceDisplayHighlightSpan {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    margin: 0.25rem;
    font-size: 18px;
    color: #fff;
    display: inline-block;
    line-height: 20px;
}

.CoreferenceDisplayGroupHeader {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    margin: 0.5rem;
    font-size: 20px;
    color: #131313;
    display: inline-block;
    line-height: 20px;
}

.BlankSpan {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    background: inherit;
    position: relative;
    margin-left: 0rem;
    padding-left: 0rem;
    margin-right: 0rem;
    padding-right: 0rem;
}

.TagStyle {
    font-family: 'Maven Pro', sans-serif;
    color: #583fcf;
    font-weight: 500;
    font-size: 0.75em;
    line-height: 1;
    white-space: nowrap;
    text-transform: uppercase;
    background: #f9f9f9;
    margin: 0;
    padding: 1px;
    position: absolute;
    top: -15px;
    left: 0px;
    cursor: pointer;
}

.ConnectTagStyle {
    font-family: 'Maven Pro', sans-serif;
    font-weight: 500;
    font-size: 0.75em;
    line-height: 1;
    white-space: nowrap;
    text-transform: uppercase;
    background: #f9f9f9;
    margin: 0;
    padding: 1px;
    position: absolute;
    bottom: -15px;
    left: 0px;
    cursor: pointer;
}

.SmallTagStyle {
    font-family: 'Maven Pro', sans-serif;
    color: #583fcf;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1;
    background: #f9f9f9;
    white-space: nowrap;
    margin: 0.1rem;
    padding: 0.1rem;
    position: absolute;
    font-size: 0.65em;
    top: -16px;
    left: -2px;
    cursor: pointer;
    z-index: 1;
}

.SentimentSelectorFloatStyle {
    font-family: 'Oswald', sans-serif;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    line-height: 1;
    color: #583fcf;
    white-space: nowrap;
    font-size: 0.7em;
    text-transform: uppercase;
    background: rgba(0, 0, 0, 0);
    margin: 0rem;
    padding: 0.1rem;
    border: 0;
    position: absolute;
    top: -38px;
    left: 72px;
    cursor: pointer;
    z-index: 5;
}

.NoSelect {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none;
}

.RichInnerTagStyle {
    font-family: 'Oswald', sans-serif;
    font-weight: 500;
    font-size: 0.75em;
    padding: 0 2px 0;
    line-height: 1;
    margin-left: 2px;
    margin-right: 2px;
    color: rgba(0, 0, 0, 1);
    box-sizing: border-box;
    border: 1px solid;
}
