.PanelButton {
    position: relative;
    overflow: hidden;
    font-family: 'Roboto', sans-serif;
    min-width: 80px;
}

.PanelButton input {
    cursor: inherit;
    min-height: 100%;
    min-width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    font-size: 999px;
    z-index: 999;
}

/* Example stylistic flourishes */

.SideBarMain {
    padding: 10px;
    font-family: 'Roboto', sans-serif;
    color: #fff
}

.PanelButton input {
    cursor: pointer;
}

.TaskPanelText {
    font-family: 'Oswald', sans-serif;
    font-size: 1.2em;
    font-weight: 700;
}

.TaskPanelSmallText {
    font-family: 'Oswald', sans-serif;
    font-size: 0.8em;
}

.TaskLabelContainer {
    padding: 0;
    border: 0 none;
    display: flex;
    margin: 0;
    font-size: 16px;
    text-align: right;
    justify-content: space-between;
    font-family: sans-serif;
}

.TaskLabel {
    padding: 0;
    border: 0 none;
    display: block;
    margin: 0 10px 0 0;
    text-align: right;
    font-family: 'Oswald', sans-serif;
    font-weight: 700;
}

.DropdownWarning {
    margin-top: -5px;
    font-weight: bold;
    font-size: 0.65rem;
    text-transform: uppercase;
    color: #dc3545;
}

.PanelSection {
    margin: 10px 0 2px;
    padding: 0;
}