.FileContainer {
    position: relative;
    overflow: hidden;
    font-family: 'Roboto', sans-serif;
    min-width: 80px;
}

.FileContainer input {
    min-height: 0%;
    min-width: 0%;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.CheckBox {
    transform: scale(1.0);
}

/* Example stylistic flourishes */

.FileContainer input {
    cursor: pointer;
}
