#selected-entities-side-panel__container {
    margin: 5px 0px;
    font-family: 'Oswald', sans-serif;
}

.selected-entities-side-panel__content-container {
    max-height: 500px;
    overflow: auto;
}

#selected-entities-side-panel__copy,
#selected-entities-side-panel__remove {
    font-size: 15px;
    margin: 4px;
    min-width: 0;
    cursor: pointer;
    background-color: white;
    border: 2px lightgray solid;
    border-radius: 2px;
    padding: 2px 4px;
    display: inline-block;
}

#selected-entities-side-panel__remove {
    color: red;
}
