}

.EventsNavItemTitle {
    cursor: pointer;
    font-family: Oswald, sans-serif;
    font-size: 14px;
    font-weight: 500;
    user-select: none;
}

.EventTag {
    font-Family: Oswald, sans-serif;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    margin-right: 5px;
}