.semi-structured-entity-list-item__container {
    font-family: 'Oswald', sans-serif;
    flex-direction: column;
    justify-content: center;
}

.semi-structured-entity-list-item__full-text {
    margin: 5px;
}

.semi-structured-entity-list-item__tag,
.semi-structured-entity-list-item__connection {
    font-family: 'Oswald', sans-serif;
    color: #f9f9f9;
    font-weight: bold;
    border-radius: 2px;
    padding: 2px 4px;
    margin-right: 5px;
    border-width: 0px;
}

.semi-structured-entity-list-item__tag {
    background-color: #17a2b8 !important;
}

.selected-entity__tag {
    background-color: #17a2b8 !important;
}

.semi-structured-entity-list-item__unselected-text {
    color: lightgray;
    font-size: smaller;
}

.semi-structured-entity-list-item__unassigned-tag {
    background-color: red !important;
}
