.PropertyTagContainer {
    margin-right: 10px;
    margin-bottom: 15px;
    float: left;
}

.PropertyTag {
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding: 5px;
}

.RemoveButton {
    font-size: 8px;
    margin-left: 4px;
    padding: 2px;
    font-weight: bold;
    /* Have to add the below 2 lines to make this button clickable
       Something to due with the float:left

       According to: https://stackoverflow.com/questions/47453561/cant-click-on-button-when-float-right
    */
    position: relative;
    z-index: 99;
}

.TagDropDown {
    font-family: "Oswald", sans-serif;
    font-size: 14px;
    font-weight: bold;
}

.TagDropDownItem {
    font-size: 14px;
}

.SelectorDropDown {
    font-Family: Oswald, sans-serif;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    margin-right: 5px;
}