.AnnotatorText {
    line-height: 3.0;
}

.OverlayAnnotatorText {
    line-height: 4.5;
}

.TagGroup {
    block: inline-block
}

.TagGroupSection {
    /* This will be twice as */
    /* big as the small item. */
    flex: 10 0 0;
}

.FlexHeader {
    display: flex;
    flex-direction: row;
}

.AnnotatorTitle {
    font-family: Oswald, sans-serif;
    font-size: 32px;
    border: 0;
}

.HoverText {
    font-family: Maven Pro, sans-serif;
    font-weight: 500;
    font-size: 14px;
}

.AnnotatorTextView {
    font-size: 18px;
    overflow: scroll;
    overflow-x: hidden;
    max-height: 800px;
    position:relative;
    padding: 20px;
}