#semi-structured-entity-list-side-panel__container {
    font-family: 'Oswald', sans-serif;
}

#semi-structured-entity-list-side-panel__list-container {
    max-height: 500px;
    overflow: auto;
}

#semi-structured-entity-list-side-panel__title {
    border: 0px;
    color: black !important;
}

#semi-structured-entity-list-side-panel__remove-all {
    font-size: 15px;
    margin: 4px;
    min-width: 0;
    cursor: pointer;
    background-color: white;
    border: 2px lightgray solid;
    border-radius: 2px;
    padding: 2px 4px;
    display: inline-block;
    color: red;
}