.coref-groups-display .coref-group-column {
    line-height: 2.0;
}


.coref-groups-display .coref-group-column .coref-group {
    padding: 1rem 0;
    border: 1px solid #e9e9e9;
    padding: 0.75rem;

    background-color:white;
    display:flex;
    flex-direction:row;

    cursor:pointer;
}

.readonly {
    background-color:#f9f9f9;
}   

.coref-groups-display .coref-group-column .coref-group.dragging {
    background-color: #e8f5e9 !important;
}




.coref-groups-display .coref-group-column .coref-group .handle {
    flex: 0 0 12px;
    width: 12px;
    min-height:100%;
    opacity: 0.40;
    margin-right: 0.5rem;
    background:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAAHUlEQVQYV2NkYGBIY0AFs0BcRupKoFkB4YLswAoAjjIEB+9cltcAAAAASUVORK5CYII=);
}
