#metadata-side-panel__container {
    font-family: 'Oswald', sans-serif;
    margin-bottom: 5px;
}

#metadata-side-panel__document-id {
    font-size: 15px;
    margin: 4px;
    min-width: 0;
}

.metadata-side-panel__item {
    padding: 3px 6px;
    border: 1px solid grey;
    margin: 3px;
    min-width: 0;
}

.metadata-side-panel__toggle-switch {
    margin-bottom: 10px !important;
}

.metadata-side-panel__card-header {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
