.toggle-switch__container {
    width: 100%;
    margin-bottom: 5px;
    padding-left: 5px;
    display: flex;
    align-items: center;
}

/* toggle in label designing */
.toggle-switch__toggle {
    position: relative;
    width: 45px;
    height: 25px;
    border-radius: 50px;
    margin-bottom: 0px;
}

/* After slide changes */
.toggle-switch__toggle:after {
    content: '';
    position: absolute;
    width: 19px;
    height: 19px;
    border-radius: 50%;
    top: 1px;
    left: 1px;
    transition: all 0.5s;
}

/* Checkbox cheked effect */
.toggle-switch__checkbox:checked + .toggle::after {
    left: 20px;
}

/* Checkbox vanished */
.toggle-switch__checkbox {
    display: none;
}

.toggle-switch__text {
    margin-left: 5px;
}
